* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
}
.main .loading {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(42, 41, 41, 0.593);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4rem;
}
.main textarea {
  margin: 1rem;
  text-align: center;
  border-radius: 5px;
}
.main button {
  font-size: 1.25rem;
  padding: 0.25rem;
  border-radius: 5px;
  margin-bottom: 1rem;
  cursor: pointer;
}
.main hr {
  width: 30rem;
  border: solid 1px rgb(222, 222, 222);
  margin: 0.5rem;
}
.main .err {
  color: red;
}
.main .messageList {
  border: solid 1px gray;
  border-radius: 5px;
  margin: 0.75rem;
  padding: 0.5rem;
  width: 20rem;
}
.main .messageList h6 {
  font-weight: 400;
  border-top: solid 1px rgb(197, 197, 197);
  padding-top: 0.5rem;
  margin-top: 0.5rem;
  color: rgb(124, 124, 124);
}/*# sourceMappingURL=style.css.map */